body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  background-color: black;
  color: white;
  padding: 20px 70px;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter
{
  height:18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  right:7px;
  top:-6px;
}
.cart-icon
{
  display: flex;
}
.icon{
  right:2px;
    font-size: 30px;
}
img
{
  height: 150px;
}
.card-wrapper{
  display: inline-flex;
   width:320px; 
  box-shadow: 0px 0px 7px black;
  border-radius: 5px;
  
}
.image-wrapper
{
  width:130px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(143, 139, 139);
}
.content
{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}
.price
{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity
{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid grey;
}
.quantity div{
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity > div:first-child,
.quantity > button
{
  background-color:rgb(0, 0, 111);
  color: white;
  border-radius: 0;
}
.quantity >button:disabled
{
  opacity: 0.7;
}
main{
  padding:20px;
  display: flex;
  flex-wrap:wrap;
  min-height: 70vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
.total
{
  box-sizing: border-box;
  position: fixed;
  background-color: gray;
  padding: 17px 40px;
  padding-right:20px;
  bottom: 0;
  width:100%;
  text-align: right;
}